import React from 'react';

import { Result, Button } from 'antd';

export const Page404 = (props) => {

    return (
        <Result
            status="404"
            title="404"
            subTitle="Sorry, the page you visited does not exist."
            extra={<Button type="primary" onClick={()=>{

                props.history.push('/login')
            }}>Back Home</Button>}
        />
    )
}

